import { Component, OnInit } from '@angular/core';
import { SwPush } from '@angular/service-worker';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  constructor(private swPush: SwPush) {}

  // tslint:disable-next-line: typedef
  async ngOnInit() {
    if (!('Notification' in window)) {
      console.error(' Notification');
      return;
    }

    if (Notification.permission !== 'granted') {
      await Notification.requestPermission();
    }

    if (Notification.permission !== 'granted') {
      console.error('');
      return;
    }

    const swPushSubscription = await this.swPush.requestSubscription({
      serverPublicKey: 'BGfr6xv-2OqB7_NmYF-Pno3UCRP7j84GfvMY9CnpLuHfUj4yXzRIEg7dBTMc7kYLM68fipFQL6mRytYS8SLPzc4'
    });

    console.log('swPushSubscription.toJSON() :>> ', swPushSubscription.toJSON());
  }
}
